@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.main-content {
  border: 0.15vw solid #969696;
  background-color: #070726;
}

.who-text {
  padding: 2.5vw 10vw;
  white-space: nowrap;
}

.name {
  font-size: 4vw;
  font-weight: normal;
  margin-bottom: 0.75vw;
  font-family: 'Noto Sans', sans-serif;
}

.titles {
  font-size: 2.5vw;
  color: #DFDFDF;
  font-family: 'Noto Sans', sans-serif;
}
.card {
    /* display: inline-block; */
    border: 0.15vw solid #969696;
    background-color: #070726;
    width: 100%;
    margin-right: 2vw;
    padding: 0.75vw 0;
}

.card:hover {
    background-color: #030318;
}

.card:last-child {
    margin-right: 0;
}

.card-text {
    font-family: 'Roboto', sans-serif;
    font-size: 2.25vw;
    color: #DFDFDF;
}


.contacts {
    display: flex;
    justify-content: center;
    margin-top: 2vw;
}

.logo-img {
    width: 4vw;
}

.logo {
    margin-right: 2vw;
}

.logo:last-child {
    margin-right: 0;
}
* {
  margin: 0;
  padding: 0;
  color: #FFF
}

.center-content {
  position: absolute;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  margin-top: -3%;
}

.background {
  width: 100%;
  height: 100%;
  position: fixed;
}

.container {
  position: relative;
}

.avatar {
  -webkit-transform: translate(0, 20%);
          transform: translate(0, 20%);
  border-radius: 50%;
  height: 13vw;
}

.info-cards {
  display: flex;
  margin-top: 2vw;
}



